import React, { Component } from 'react';

import './landing_page.css';
import CircleIllustration from './CircleIllustration.jsx';

class LandingPage extends Component {

	renderText() {
		return (
			<div className="landing_page__text">
        <div className="landing_page__title">
        	<p> Welcome to Eddies cool internet site. </p>
        	<p> Eddie Jiao is an engineer who understands product.
He graduated from Brown in 2019 and is currently at Slack in San Francisco. </p>
        </div>
			</div>
		);
	}

	renderAltText() {
		return (
			<div className="landing_page__text">
        <div className="landing_page__title">
        	<p> Welcome to Eddies cool internet site. </p>
        </div>
        <div className="landing_page__subtitle">
      		<p> Eddie Jiao is an engineer who understands product.
He graduated from Brown in 2019 and is currently at Slack in San Francisco. </p>
        </div>
			</div>
		);
	}

	renderNav() {
		return(
			<div className="nav">
				<a href='#'>WORK</a>
				<a href='#'>BLOG</a>
				<a href='#'>ABOUT</a>
			</div>
		);
	}

  render() {
    return (
      <div className="landing_page">
      	<div className="landing_page__content">
        	{this.renderText()}
      		<CircleIllustration />
      	</div>
			</div>
    );
  }
}

export default LandingPage;



